import React from "react";
import "hui/step-flow/flow-confirmation";

export default class HAFlowConfirmation extends React.Component {
    static propTypes = {
        className: React.PropTypes.string,
        titleText: React.PropTypes.string,
        subtitleText: React.PropTypes.string
    };

    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <ha-flow-confirmation class={this.props.className} {...this.props}>
                {this.props.children}
            </ha-flow-confirmation>
        );
        /* jshint ignore:end */
    }
}
